<template>
  <div class="page-owasp-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">网站防护</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/protection/' + domain }">防护配置</el-breadcrumb-item>
        <el-breadcrumb-item>web防护引擎</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <div>
          <el-form :model="webEngineForm" :rules="rules" ref="webEngineForm" label-width="160px" class="engine-form">
            <el-form-item label="SQL注入防护">
              <el-radio-group v-model="webEngineForm.sql_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="XSS防护">
              <el-radio-group v-model="webEngineForm.xss_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="命令执行防护">
              <el-radio-group v-model="webEngineForm.cmd_exec_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="代码执行防护">
              <el-radio-group v-model="webEngineForm.code_exec_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="WebShell上传防护">
              <el-radio-group v-model="webEngineForm.webshell_update_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="敏感文件泄露防护">
              <el-radio-group v-model="webEngineForm.sensitive_file_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="路径穿越防护">
              <el-radio-group v-model="webEngineForm.path_traversal_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="高危Nday防护">
              <el-radio-group v-model="webEngineForm.high_nday_check">
                <el-radio label="block">开启</el-radio>
                <el-radio label="close">关闭</el-radio>
                <el-radio label="watch">观察</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
        </div>
        <el-row type="flex" justify="space-between" class="margin-border">
          <el-col :span="12">
            <a class="el-button el-button--primary is-plain" :href="'/#/protection/' + domain">返回</a>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickWebEngineFormSubmit('webEngineForm')"
              :loading="loading">保存</el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      domain: this.$route.params.domain,
      webEngineForm: {},
    };
  },
  computed: {
    rules() {
      return {

      }
    }
  },

  mounted() {
    const route = useRoute()
    this.domain = route.params.domain;
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var getUrl = "/waf/waf_get_web_engine_protection";
      var oData = { domain: t.domain };
      JXAjax(
        "post",
        getUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.webEngineForm = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onClickWebEngineFormSubmit(webEngineForm) {
      var t = this;
      var postUrl = "/waf/waf_edit_web_engine_protection";
      this.webEngineForm.domain = t.domain;
      this.$refs[webEngineForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.webEngineForm,
            function (response) {
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
  },
};
</script>
<style>
.page-owasp-wrap {
  max-width: 800px;
  min-width: 400px;
}

.page-owasp-wrap .match-inline-block {
  width: 192px;
}

.engine-form .el-form-item__content {
  margin-left: 40px;
}
</style>
